function ConOneBox({conOneBox}){
    return(
        <div>
            <div className="con1_list_box_one">
                <img src={conOneBox.image} alt="conOneBox_image" />
                <p className="con1_list_box_one_name">{conOneBox.name}</p>
                <p className="con1_list_box_one_price"><span>{conOneBox.price}</span></p>
            </div>
        </div>
    )
}


